.bio-container {
    flex: 1 1 20%;
    min-height: 100vh;
    background-color: var(--bio-bg-color);
    text-align: center;
    padding: 30px 12px;
    border-right: 5px solid var(--bio-border-color);

    h2 {
        margin-bottom: 25px;
    }
    
    img {
        width: 150px;
        height: 145px;
        border-radius: 50%;
        margin-bottom: 25px;
    }
    
    p {
        margin-bottom: 20px;
    }
    
    .welcome-text {
        font-weight: bold;
    }

    .social-container {
        display: flex;
        justify-content: center;
        list-style: none;
        border-bottom: 1px solid var(--border-color);
        margin-bottom: 25px;
        padding-bottom: 25px;

        li {
            flex: 1 1 0;
            max-width: 60px;

            a {
                color: var(--main-color);
                font-size: 30px;
            }
        }
    }

    .email-container {
        display: flex;
        justify-content: center;

        ion-icon {
            color: var(--main-color);
            font-size: 20px;
            max-width: 20px;
            margin-right: 5px;
        }

        a {
            color: var(--main-text-color);
            text-decoration: none;
            max-width: 225px;
        }
    }
    
}

/* Mobile */
@media(max-width: 450px) {
    .bio-container {
        min-height: auto;
        border-right: none;
        border-bottom: 5px solid var(--bio-border-color);
        
        h2 {
            display: none;
        }
        
        p {
            max-width: 60%;
            margin: 10px auto;
        }
    }
}