.about-container {
    flex: 1 1 80%;
    min-height: 100vh;
    background-color: var(--about-bg-color);
    padding: 50px;

    .name {
        font-size: 42px;
        margin-bottom: 15px;
    }
    
    .title {
        font-size: 24px;
        margin-bottom: 15px;
        font-weight: bold;
    }

    .description {
        max-width: 75%;
        margin-bottom: 10px ;
    }

    .btn-projects {
        font-weight: bold;
        font-size: 16px;
        color: var(--main-text-color);
        background-color: var(--main-color);
        border: 2px solid var(--main-color);
        border-radius: 5px;
        text-decoration: none;
        transition: .5s;
        margin: 25px 0;
        padding: 12px 10px;
        width: 150px;
        text-align: center;
        display: flex;

        ion-icon, span {
            flex: 1 1 0;
        }

        ion-icon {
            font-size: 20px;
            max-width: 20px;
        }
    }

    .btn-projects:hover {
        background-color: transparent;
    }

    /* Skills container */
    .skills-section-title {
        border-top: 1px solid var(--border-color);
        padding-top: 20px;
        margin-bottom: 20px;
        font-size: 32px;
    }

    .skills-container {
        display: flex;
        flex-wrap: wrap;
        margin-top: 25px;

        .skills-box {
            flex: 1 1 33%;
            max-width: 33%;
            margin-bottom: 35px;

            i {
                font-size: 40px;
                margin-right: 10px;
            }
            
            .skills-title {
                font-size: 24px;
                margin-bottom: 25px;
                font-weight: bold;
                padding-left: 10px;
                border-left: 5px solid var(--main-color);
            }
        }
    }
}

/* Mobile */
@media(max-width: 450px) {
    .about-contaier {
        text-align: center;
        padding: 30px;
    }
  
    .description {
        margin: 10px auto !important;
        max-width: 100% !important;
        line-height: 25px;
    }
  
    .btn-projects {
        margin: 20px auto !important;
    }
  
    .skills-box {
        flex: 1 1 100% !important;
        max-width: 100% !important;
        margin-bottom: 40px !important;
        text-align: left;
  
        i {
          font-size: 60px !important;
        }
    }
}